.formControl.error input,
.formControl.error textarea
{
    border: solid red 2px;
}

.formControl.error span
{
    color: red;
}

.formSummaryError {
    border: red 1px solid;
    padding: 5px;
    color: red;
}

.formBlock b {
    color: black;
}

.loginForm {
    color: black;
}

.headings {
    display: flex;
    align-items: baseline;
}

.headings h1 {
    float: left;
    text-align: left;
}

.headings h3 {
    float: right;
    text-align: right;
    font-weight: normal;
}

.headings h3 span {
    font-weight: bolder;
}

.headings span:before {
    content:" ";
    display:inline-block;
    width:7px;
}